.Home {
  background: url('../../assets/img/home/background-home-desktop.jpg') center center/cover;
}

.Home__container {
  margin: 0 auto;

  max-width: 1200px;
  width: 100%;
  height: 100vh;

  padding: 131px 45px;

  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.Home__item {
  max-width: 450px;

  display: flex;
  flex-direction: column;
  gap: 24px;
}

.Home__text {
  color: var(--accent);
}

.Home__explore {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--primary);
  width: 274px;
  height: 274px;

  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: .2s all ease-out;
  text-decoration: none;
}

/* .Home__explore a {
  text-decoration: none;
} */

.Home__explore:hover {
  box-shadow: 0 0 0 88px rgba(255, 255, 255, .1);
}

.Home__explore h4 {
  color: #0B0D17;
}

@media (max-width: 768px) {
  .Home {
    background: url('../../assets/img/home/background-home-tablet.jpg') center center/cover;

  }

  .Home__container {
    flex-direction: column;
    align-items: center;

    padding-top: 202px;
    padding-bottom: 90px;
  }

  .Home__item {
    align-items: center;
  }

  .Home__suptitle {
    font-size: 20px;
    line-height: 24px;
  }

  .Home__title {
    line-height: 150px;
  }

  .Home__text {
    text-align: center;
    font-size: 16px;
    line-height: 28px;
  }

  .Home__explore {
    width: 242px;
    height: 242px;
  }

  .Home__explore:hover {
    box-shadow: 0 0 0 78px rgba(255, 255, 255, .1);
  }
}

@media (max-width: 425px) {
  .Home {
    background: url('../../assets/img/home/background-home-mobile.jpg') center center/cover;

  }

  .Home__container {
    padding: 112px 24px 48px;

  }

  .Home__item {
    gap: 16px;
  }

  .Home__suptitle {
    font-size: 16px;
    line-height: 19px;
  }

  .Home__title {
    font-size: 80px;
    line-height: 100px;
  }

  .Home__text {
    font-size: 15px;
    line-height: 25px;
  }

  .Home__explore {
    width: 150px;
    height: 150px;
  }

  .Home__explore:hover {
    box-shadow: 0 0 0 48px rgba(255, 255, 255, .1);
  }

  .Home__explore h4 {
    font-size: 20px;
    line-height: 23px;
  }
}